<template>
  <div id="app">        
    <h2>Xin chào: {{hoTen}}</h2>
    <div class="trac-nghiem">
      <div class="cau-hoi">
        <h3 v-html="questions[curr].content"></h3>
        <div class="tra-loi">
            <ul>
                <li v-for="(ans,index) in questions[curr].answers" :key="index">                                      
                  <label :for="index">
                      <input v-if = "questions[curr].multi == false" type="radio" @click="choose(ans,questions[curr].id)" :id="index" :name="index" :checked="ans.choose">
                      <input v-else type="checkbox" :id="index" :name="index" @click="choose(ans)" v-model="ans.choose">
                      {{ans.content}}                    
                  </label>                                              
                </li>                
            </ul>            
        </div>
      </div>
      <div class="taskbar">
          <button v-bind:disabled = "curr==0" @click="prev">Prev</button>
          <button v-if = "curr!=questions.length - 1" @click="next">Next</button>
          <button v-else @click="finish">Finish</button>
      </div>      
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      hoTen:'Nguyen Tien Dat',
      curr: 0,
      clientAnswers: [], 
      questions:[
          {     
            id:1,       
            content: 'Học lập trình cần công cụ gì?', 
            answers:
            [
                {content: "Chổi", correct: false, choose: false},
                {content: "Máy tính", correct: true, choose: false},
                {content: "Cuốc", correct: false, choose: false},
                {content: "Xẻng", correct: false, choose: false}
            ],
            multi:false
          },
          {       
            id:2,      
            content: 'Thầy Nguyện đẹp zai không?',
            answers:
            [
                {content: "Không", correct: false, choose: false},
                {content: "Có", correct: true, choose: false}
            ],
            multi:false
          },
          {     
            id:3,       
            content: 'Lập trình để làm gì?',
            answers:
            [
                {content: "Để FA", correct: false, choose: false},
                {content: "Làm ruộng", correct: false, choose: false},
                {content: "Chẳng để làm gì", correct: false, choose: false},                                
                {content: "Giúp cuộc sống đơn giản hơn", correct: true, choose: false}
            ],
            multi:false
          },
          {       
            id:4,      
            content: 'Bạn có thích học lập trình?',
            answers:
            [
                {content: "Không", correct: false, choose: false},
                {content: "Có", correct: true, choose: false}
            ],
            multi:false
          }
      ]
    }
  },
  methods:{
      next() {          
          if(this.curr < this.questions.length - 1){
              this.curr++;            
          }           
      },

      prev() {
            if(this.curr > 0)
              this.curr--;
      },

      choose(ans,id) {                               
          this.questions[this.curr].answers.forEach(_ans => {
              if(_ans == ans)
                _ans.choose = true;
              else
                _ans.choose = false;              
          })                   
          //Kiểm tra đúng sai
          if(!this.clientAnswers.includes(id) && ans.correct == true)
          {                                
              this.clientAnswers.unshift(id);                                
          }                
          else if(this.clientAnswers.includes(id) && ans.correct == false){                
              this.clientAnswers.splice(this.clientAnswers.indexOf(id),1);                                
          }                                                
      },

      finish() {
          alert('Ban tra loi dung: ' + this.clientAnswers.length + '/' + this.questions.length);
      }
  }
}
</script>

