  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;  
  color: #2c3e50;
  margin:10px auto;
  width:600px;
}
#app h2,
.cau-hoi > h3
{
    margin-bottom:20px;
}
.trac-nghiem{   
    margin: 20px auto;
    border:1px solid #727272;
    padding:20px;    
}
.tra-loi{
    height:120px;
}
.tra-loi ul{
    margin-bottom:15px;
}
.tra-loi input{
    margin-right:10px;
    cursor: pointer;
}
.tra-loi li label{
    display:inline-block;
    cursor: pointer;
    line-height:26px;
}
.taskbar{
    display:flex;
    justify-content:space-between;
} 
.taskbar button{
    display:block;
    padding:3px 15px;    
}

</style>
